<template>
    <div class="addWarp">
        <div class="rating_page">
            <header>
                <router-link to="/address">
                    <i class="el-icon-arrow-left"></i>
                </router-link>
                <span>新增地址</span>
            </header>

            <div class="adddetail">
                <div class="adddetail_div">

                    <div class="input_new">
                        <input type="text" placeholder="请填写你的姓名" class="input1" v-model="name">
                    </div>

                    <div class="input_new" @click="adddetail">
                        <input type="text" placeholder="小区/写字楼/学校等" class="input1" v-model="address">
                    </div>

                    <div class="input_new">
                        <input type="text" placeholder="请填写详细送餐地址" class="input1" v-model="address_detail">
                    </div>
                    <div class="input_new">
                        <input type="tel" placeholder="请填写能够联系到您的手机号" class="input1" maxlength="11" v-model="phone">
                    </div>
                    <div class="input_new">
                        <input type="tel" placeholder="备用联系电话（选填）" class="input1" maxlength="11" v-model="phone_bk">
                    </div>
                </div>
            </div>
            <div class="alet_container  animated bounceIn" v-if="isShow1">
                <section class="tip_text_container">
                    <div class="tip_icon">
                        <span class="tip_icon_s1"></span>
                        <span class="tip_icon_s2"></span>
                    </div>
                    <p class="tip_text">收货人姓名错误</p>
                    <div class="confrim" @click="isShow1 = !isShow1">确认</div>
                </section>
            </div>
            <div class="alet_container  animated bounceIn" v-if="isShow2">
                <section class="tip_text_container">
                    <div class="tip_icon">
                        <span class="tip_icon_s1"></span>
                        <span class="tip_icon_s2"></span>
                    </div>
                    <p class="tip_text">地址信息错误</p>
                    <div class="confrim" @click="isShow2 = !isShow2">确认</div>
                </section>
            </div>
            <div class="alet_container  animated bounceIn" v-if="isShow3">
                <section class="tip_text_container">
                    <div class="tip_icon">
                        <span class="tip_icon_s1"></span>
                        <span class="tip_icon_s2"></span>
                    </div>
                    <p class="tip_text">地址信息错误</p>
                    <div class="confrim" @click="isShow3 = !isShow3">确认</div>
                </section>
            </div>
            <div class="alet_container  animated bounceIn" v-if="isShow4">
                <section class="tip_text_container">
                    <div class="tip_icon">
                        <span class="tip_icon_s1"></span>
                        <span class="tip_icon_s2"></span>
                    </div>
                    <p class="tip_text">添加地址成功</p>
                    <div class="confrim" @click="isShow4 = !isShow4">确认</div>
                </section>
            </div>

            <div class="addbutton">
                <el-button class="addbtn" @click="newAddress">新增地址</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Add",
        data(){
            return {
                msite:"",
                address:"",
                name:"",
                address_detail:"",
                phone:"",
                phone_bk:"",
                user_id:"",
                geohash:"",
                tag:"家",
                sex:1,
                tag_type:2,
                isShow1:false,
                isShow2:false,
                isShow3:false,
                isShow4:false,
                a:[]
            }
        },
        created(){
            this.name = JSON.parse(localStorage.getItem('name'));
            this.address = JSON.parse(localStorage.getItem('add'));
            this.user_id = JSON.parse(localStorage.getItem('user_id'));
            this.msite = JSON.parse(localStorage.getItem('msite'));
            this.geohash = this.msite.geohash
            localStorage.removeItem("add");
            // console.log(this.address)
        },
        methods:{
            newAddress(){
                this.axios.post("/v1/users/"+ this.user_id + "/addresses",{
                    user_id:this.user_id,
                    address:this.address,
                    address_detail:this.address_detail,
                    geohash:this.geohash,
                    name:this.name,
                    phone:this.phone,
                    tag:this.tag,
                    sex:this.sex,
                    phone_bk:this.phone_bk,
                    tag_type:this.tag_type
                }).then(res=>{
                    console.log(res);

                    if (res.message == "收货人姓名错误" || this.name == ""){
                        this.isShow1 = true;
                    }else if (res.message == "地址信息错误" || this.address == "" || this.address_detail == ""){
                        this.isShow2 = true;
                    }else if (res.message == "收获手机号错误") {
                        this.isShow3 = true;
                    }else if (res.success == "添加地址成功") {
                        this.isShow4 = true;
                        this.name = "";
                        this.address = "";
                        this.address_detail = "";
                        this.phone = "";
                        this.phone_bk = "";
                        localStorage.removeItem("name");
                    }else {
                        this.isShow1 = false;
                        this.isShow2 = false;
                        this.isShow3 = false;
                    }
                }).catch(err=>{})
            },
            adddetail(){
                localStorage.setItem('name',JSON.stringify(this.name));
                this.$router.push("/adddetail");
            }
        }
    }
</script>

<style scoped>
    .addWarp{
        width: 100%;
        overflow: hidden;
    }
    a{
        text-decoration: none;
    }
    header>a{
        color: #fff;
        position: absolute;
        top: 50%;
        left: .4rem;
        transform: translateY(-50%);
    }
    header span{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%,-50%);
    }

    .rating_page{
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #f2f2f2;
        z-index: 202;
        padding-top: 1.95rem;
    }

    .adddetail{
        margin-top: .4rem;
    }
    .adddetail_div{
        background: #fff;
        padding-top: .4rem;
    }
    .input_new{
        padding-bottom: .4rem;
    }
    .input1{
        display: block;
        width: 15rem;
        font-size: .6rem;
        margin: 0 auto;
        padding: .3rem;
        background: #f2f2f2;
        border: 1px solid #ddd;
        border-radius: 3px;
        box-sizing: border-box;
    }
    .addbutton{
        margin: .6rem auto;
        width: 15rem;
        background: #4cd964;
        border-radius: 5px;
    }
    .addbtn{
        width: 100%;
        font-size: .6rem;
        color: #fff;
        background: none;
        font-weight: 700;
        border-radius: 5px;
        outline: none;
    }

    .alet_container{
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 200;
    }
    .tip_text_container{
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -6rem;
        margin-left: -6rem;
        width: 12rem;
        animation: tipMove .4s;
        background-color: #fff;
        padding-top: .6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        border: 1px;
        border-radius: .25rem;
    }
    .tip_icon{
        width: 3rem;
        height: 3rem;
        border: .15rem solid #f8cb86;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
    .tip_icon_s1{
        width: .12rem;
        height: 1.5rem;
        background-color: #f8cb86;
    }
    .tip_icon_s2{
        width: .2rem;
        height: .2rem;
        border: 1px;
        border-radius: 50%;
        margin-top: .2rem;
        background-color: #f8cb86;
    }
    .tip_text{
        font-size: .7rem;
        color: #333;
        line-height: .9rem;
        text-align: center;
        margin-top: .8rem;
        padding: 0 .4rem;
    }
    .confrim{
        font-size: .8rem;
        color: #fff;
        font-weight: 700;
        margin-top: .8rem;
        background-color: #4cd964;
        width: 100%;
        text-align: center;
        line-height: 1.8rem;
        border: 1px;
        border-bottom-left-radius: .25rem;
        border-bottom-right-radius: .25rem;
    }
</style>